import Vue from 'vue'
import VueRouter from 'vue-router'
const Home = () =>
    import ('../views/home/Home.vue')
const Cart = () =>
    import ('../views/cart/Cart.vue')
const cateGory = () =>
    import ('../views/category/CateGory.vue')
const Profile = () =>
    import ('../views/profile/Profile.vue')
const detail = () =>
    import ('../views/detail/Detail.vue')
Vue.use(VueRouter)

const routes = [{
        path: '',
        redirect: '/home'
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/cart',
        component: Cart
    },
    {
        path: '/category',
        component: cateGory
    },
    {
        path: '/profile',
        component: Profile
    },
    {
        path: '/detail/:id',
        component: detail
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router